import back from '@/components/back'
import noData from '@/components/noData'

export default {
    components:{
        back,
        noData
    },
    data(){
        return{
            search: this.$store.state.searchText,
            id: this.$route.query.id,
            list: [],
            Um: [],
            from: this.$route.query.from,
            page: 1,
            size: 10,
            haveMore: true,
			listShow: true,
            user: this.$store.state.userInfo,
            ListDetails: this.$store.state.checkCkooseList,
            showConfirm: false,
            pageShow: false,
            medSearchList:null,
            inputFlag: false,
			scrollTopDistance: 0,
			censorateItemName: this.$route.query.censorateItemName,
			censorateId: this.$route.query.censorateId,
			censorateTypeId: this.$route.query.censorateTypeId,
			defaultImg: 'this.src="' + require('../../assets/images/1.jpg') + '"',
			numb:0,
        }
    },
    computed:{
        totalPrice(){
            let p = 0
            if(this.ListDetails.length){
                this.ListDetails.forEach(v => {
                    p += (v.ItemPrice * v.ItemNum)
                })
            }
            return p.toFixed(2)
        }
    },
    created(){
		
    },
    mounted(){
        this.getMore()
    },
    beforeDestroy(){
        window.onscroll = null
    },
	beforeRouteLeave(to,from,next){   //离开
		window.onscroll = null
		if(from.meta.keepAlive) {
			from.meta.scollTopPosition = this.scrollTopDistance
		}
		//this.$store.dispatch('pushcId',0)
		next()
	},
	beforeRouteEnter(to, from, next) {  //进入前 
			let flag = false
			next(vm => {
				vm.getMore()
				if(vm.$store.state.cId >= 0){
					flag = true
				}
				if(flag){
					vm.page = 1
					vm.haveMore = true
					to.meta.scollTopPosition = 0
					vm.pageShow = false
					vm.listShow = false
					if(vm.$route.query.censorateId){
						vm.pull(false,vm.$route.query.censorateItemName,vm.$route.query.censorateId,vm.$route.query.censorateTypeId)
					}
					vm.$store.dispatch('pushIsFresh',true)
					vm.search = vm.$store.state.searchText
				}
				else{
					setTimeout(()=>{
					    vm.$refs.el2.scrollTop = to.meta.scollTopPosition
					},1)
				}
			});
		},
    methods:{
		toDetail(i){
			if(this.numb == 1){
				this.$router.push(`/inspect/checkIntroduction?CensorateItemId=${i.CensorateItemId}`)
			}
		},
        searchFor(){
			this.censorateItemName = this.search;
            this.page = 1
            this.haveMore = true
			this.$store.dispatch('pushsearchText',this.censorateItemName)
			if(this.$route.query.censorateId){
				this.pull(false,this.censorateItemName,this.$route.query.censorateId,this.$route.query.censorateTypeId)
			}
            
        },
        BackTo(){
			this.$router.push(`/inspect/checklist`)
        },
        pull(h,a,b,c){
            if(this.haveMore){
                if(this.search){
                    this.id = ''
                }
                this.$post('InspectionAPI/Censorate/QueryCensorateItemPage',this.$pick({
				   CensorateItemName:a,
				   CensorateId:b,
				   CensorateTypeId:c,
				   PageIndex: this.page,
				   PageSize: this.size
                })).then(o => {
					this.numb = 0
                    this.pageShow = true
					this.listShow = true
                    if (!o.ReturnData || o.ReturnData.length < 10){
                        this.haveMore = false
                    }
                    else {
                        this.haveMore = true
                    }
                    const list = this.$store.state.checkCkooseList
					if(list){
						 list.forEach(a => {
						    o.ReturnData.forEach(v => {
						        if(v.CensorateItemId === a.CensorateItemId){
						            v.state = 'onP'
						        }
						    })
						})
					}
                   
                    if (h){
                        o.ReturnData.forEach(v => {
                            this.list.push(v)
                        })
                    }
                    else {
                        this.list = o.ReturnData
                    }
					this.$store.dispatch('pushcId','cId')
					this.numb = 1
                })
            }
        },
//         toUm(i,idx){
//             this.$post('Prescription/Medication/SaveMedicationOftenUsed',{
//                 MedicationId: i.Id,
//                 MedicationCompanyId: i.MedicationCompanyId,
//                 OrgId: this.user.Data.User.OrgId,
//                 DoctorId: this.user.Data.User.DoctorId
//             }).then(o => {
//                 this.list[idx].IsAdd = true
//             })
//         },
        toUp(i){
			i.state = 'onP'
			this.ListDetails.push(i)
			this.ListDetails.forEach((v,k) => {
				if(!this.ListDetails[k].ItemNum){
					this.ListDetails[k].ItemNum = 1
				}
			})
			this.$store.dispatch('pushcheckCkooseList',this.ListDetails)
//             if(this.$route.query.from == 'ump' || this.$route.query.from == 'upp'){
//                 this.$store.dispatch('pushcMed', i)
//                 this.$router.push('/consumption?back=-3')
//             }
//             else{
//                 this.$store.dispatch('pushcMed', i)
//                 this.$router.push('/consumption?back=-4')
//             }
        },
		getMore(){
		    const el2 = this.$refs.el2
		    let timer = null
		    el2.onscroll = () => {
		        clearTimeout(timer)
		        timer = setTimeout(() => {
					 const top = el2.scrollTop
								|| el2.pageYOffset
								|| el2.scrollTop,
		                    h = el2.scrollHeight,
		                    wh = window.innerHeight
					this.scrollTopDistance = top;
		            if (h - top - wh < 100 && this.haveMore){
		                this.page += 1
						if(this.$route.query.censorateId){
							this.pull(true,this.censorateItemName,this.$route.query.censorateId,this.$route.query.censorateTypeId)
						}
		            }
		        }, 100)
		    }
		},
        confirm(){
            this.ListDetails.forEach((v,k) => {
                if(v.CensorateItemId == this.removeI.CensorateItemId){
                    this.ListDetails.splice(k,1)
                }
            })
            this.list[this.removeIdx].state = null
        },
        delToPre(i,idx){
            this.removeI = i
            this.removeIdx = idx
            this.showConfirm = true
        },
        toList(i){
            this.search = i
            this.$store.dispatch('pushsearchText',i)
            this.medSearchList = null
            this.haveMore = true
            this.id = ''
            this.pull(false)
        },
        searchByName(){
            if(this.inputFlag){
                return
            }
            if(this.search){
				this.$post('Prescription/Medication/GetMedicationByNameForOftenUsed',this.$pick({
					MedicationName: this.search,
                    OrgId: this.user.Data.User.OrgId,
					DoctorId: this.user.Data.User.DoctorId,
					PageIndex: this.page,
					PageSize: 20
				})).then(o => {
					this.medSearchList = o.ReturnData
				})
			}
        },
    }
}



